import React from "react";
import PostListItem from "../templates/post-item";

const IndexPage = ({ data }) => {
  const { edges: posts } = data.allMarkdownRemark;
  const toDayMonth = date => {
    const [month, day] = date.split("/");

    return `${day}/${month}`;
  };

  return (
    <div className="post-list">
      {posts &&
        posts.length &&
        posts
          .sort((a, b) => {
            const first =
              a.node && a.node.frontmatter && a.node.frontmatter.date;
            const second =
              b.node && b.node.frontmatter && b.node.frontmatter.date;

            const dateA = new Date(first).getTime();
            const dateB = new Date(second).getTime();

            return dateA < dateB;
          })
          .map(({ node: post }) => (
            <PostListItem
              key={post.frontmatter.path}
              title={post.frontmatter.title}
              date={toDayMonth(post.frontmatter.date)}
              path={post.frontmatter.path}
              excerpt={post.frontmatter.excerpt}
              tags={post.frontmatter.tags}
            />
          ))}
    </div>
  );
};

export const query = graphql`
  query IndexQuery {
    allMarkdownRemark {
      totalCount
      edges {
        node {
          id
          frontmatter {
            title
            date(formatString: "MM/DD/YY")
            path
            tags
            excerpt
          }
        }
      }
    }
  }
`;

export default IndexPage;



